.App {
  margin: 1%;
  border: 2px solid black;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: repeat(2, 1fr);
  font-family: 'Bree Serif', serif;
}
/*************header**************/
.header_div{
  background-color: #7ED2D2;
  grid-area: 1 / 1 / span 1 / span 2;
  color: #FFFFFF;
  padding-top: 1%;
  padding-bottom: 1%;
}

.headertxt{
  font-size: 1.875em;
  float: left;
  font-family: 'Charm', cursive;
  margin-left: 10px;
  margin-top: 20px;
  font-weight: bold;
  font-style: italic;
}

.logo_img{
  background-color: #7ED2D2;
  height: 80px;
  width: auto;
  float: left;
  margin-left: 2%;
}

/***************main section right******************/
.directoryView_div{
  grid-area: 2 / 1 / span 1 / span 1;
  min-height: auto÷ ;
  border-right: 2px solid black;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr;
  margin-left: 5%;
  margin-right: 5%;
}

/*************search*************/
.searchForm_div{
  padding: 1%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 120px;
  grid-gap: 10px;
}

#search{
  border: 1px solid black;
  border-radius: 10px;
  min-width: 20px;
}

.btn{
  margin-top: 20%;
}

hr{
  margin-top: 27px;
  width: 90%;
}

/***************recipe*****************/
.recipeCard_div{
  height: 40px;
  border: 1px solid black;
  font-size: 15px;
  font-weight: bold;
  justify-content: center;
  margin: 10px;
  padding: 10px;
  text-transform: uppercase;
}

.recipeCard_div:hover{
  cursor: pointer;
  background-color: #7ED2D2; 
} 

/******************main right******************/
.detailView_div{
  float: right;
  grid-area: 2 / 2 / span 1 / span 1;
}

.detailView_header{
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 200px 1fr;
  grid-gap: 5px;
}

dl{
  display: grid;
  grid-template-rows: 80px 1fr;
  grid-template-columns: repeat(2, 1fr);
}

.recipe_img{
  width: 150px;
  height: 150px;
}

h3, h5{
  font-size: 20px;
  font-weight: bold;
  text-shadow: slategrey;
  margin-top: 7%;
  margin-right: 5
}

h5{
  font-size: 15px;
  color: rgb(110, 110, 110);
}